.container {
    margin: 10px;
    width: 500px;
    background-color: #fff;
    padding: 25px 30px;
    border-radius: 5px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  }
  @media (max-width: 584px) {
    .container {
      width: 400px;
    }
  }
  @media (max-width: 380px) {
    .container {
      width: 300px;
    }
  }
  .user_details {
    display: flex;
    flex-direction: column;
    margin: 10px 0 10px 0;
  }
  .user_details .input_box {
    display: flex;
    margin: 8px 0 8px 0;
    justify-content: space-between;
    align-items: center;
  }
  .user_details .input_box input {
    padding: 2px;
    width: 300px;
    border-radius: 5px;
    border: 1.5px solid #548aee;
  }
  .user_details .input_box input:hover {
    border-color: #740364;
  }
  @media (max-width: 584px) {
    .user_details .input_box input {
      width: 220px;
    }
  }
  @media (max-width: 380px) {
    .user_details .input_box input {
      width: 150px;
    }
  }