.container {
    margin: 10px;
    width: 500px;
    /* background-color: #fff; */
    padding: 5px 10px;
    border-radius: 50px;
    /* box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15); */
  }
  @media (max-width: 584px) {
    .container {
      width: 400px;
    }
  }
  @media (max-width: 380px) {
    .container {
      width: 300px;
    }
  }
  
  .input_box {
    display: flex;
    margin: 8px 0 8px 0;
    justify-content: space-evenly;
    align-items: center;
  }
  .input_box input {
    padding: 2px;
    width: 350px;
    /* border-radius: 5px; */
    background-color: transparent;
    border: 0px;
    border-bottom: 3px solid rgb(27, 25, 25);
  }
  
  .input_box input:focus {
    outline: none;
  }
  .input_box input:hover {
    border-color: rgb(24, 21, 21);
  }
  @media (max-width: 584px) {
    .input_box input {
      width: 220px;
    }
  }
  @media (max-width: 380px) {
    .input_box input {
      width: 150px;
    }
  }