.container {
    margin: 10px;
    width: 500px;
    background-color: rgb(71, 181, 206);
    padding: 5px 30px;
    border-radius: 10px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  }
  @media (max-width: 584px) {
    .container {
      width: 400px;
    }
  }
  @media (max-width: 380px) {
    .container {
      width: 300px;
    }
  }
  .item {
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .date {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .close {
    border: 0px;
    background-color: transparent;
    margin-left: 10px;
    font-size: x-large;
    font-weight: bolder;
    color: rgb(22, 10, 131);
    cursor: pointer;
  }